@import url(https://fonts.googleapis.com/css?family=Fira+Sans&display=swap);
body{margin-top:5px;padding:0;font-family:-apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}.wrapper{min-height:calc(100vh - 70px)}.footer-position{height:50px}code{font-family:source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace}

.hero{background-color:#99ffcc;font-family:'Heebo', sans-serif}.intro{margin-bottom:4rem}.logo{font-size:3.75rem;font-family:'Fira Sans', sans-serif}.card-spacing{padding:1.5rem}.icon-spacing{padding-right:4px}.heart-spacing{margin-right:3px}.profile-img{box-shadow:0px 0px 10px gray;width:80%}#about-h2{width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;margin:0 auto;padding-bottom:5%}.bio-padding{padding:0 10% 5% 10%}.img-position{text-align:center}.icon-adjust{width:60px}a{color:inherit}.footer-position{margin:0}.card-display{display:none}@media (min-width: 426px) and (max-width: 1023px){.profile-img{width:50%}}@media (min-width: 426px){.card-display{display:block;width:50%;box-shadow:0px 0px 15px slategray}.project-text{width:40%;padding:0 5%}.project{margin-bottom:10%}.logo-margin{margin:5% 5% 0px 0px}.logo-section{margin-top:5%}.portfolio-container{max-width:80vw}}

